"use client";

import Link from "next/link";
import Image from "next/image";
import { usePathname } from "next/navigation";
import { AnimatePresence, motion } from "framer-motion";
import { Outfit } from "next/font/google";
import { useEffect, useMemo, useState } from "react";
import "./home-screen-v4.css";

const outfit = Outfit({
  subsets: ["latin"],
  weight: ["400", "500", "600", "700", "800"],
});

const BANNERS = [
  {
    id: "a",
    title: "Green Light Match",
    subtitle: "Group-ready flow from search to winner in minutes.",
    cta: "Create Room",
    href: "/game-setup?theme=green-light",
  },
  {
    id: "b",
    title: "Menu First Sprint",
    subtitle: "Jump into menu swipe and shortlist top picks instantly.",
    cta: "Swipe Menu",
    href: "/swipe?mode=menu",
  },
  {
    id: "c",
    title: "Explore Pulse",
    subtitle: "Curated hotspots and shortcuts tuned for group play.",
    cta: "Open Explore",
    href: "/explore?source=home-v4",
  },
];

const TRENDY = [
  { id: "t1", title: "Late Night Winners", meta: "Open now + high ratings", href: "/swipe?preset=night" },
  { id: "t2", title: "Office Lunch League", meta: "Fast service around business zones", href: "/swipe?preset=office" },
  { id: "t3", title: "Weekend Comfort", meta: "Best group picks this week", href: "/swipe?preset=comfort" },
];

const SHORTCUTS = [
  { id: "s1", title: "Transit Hunter", detail: "Near BTS/MRT only", href: "/swipe?shortcut=transit", skip: true },
  { id: "s2", title: "Diet Lock", detail: "Vegan and halal boost", href: "/game-setup?shortcut=diet", skip: false },
  { id: "s3", title: "Budget Arena", detail: "Price cap and top rated", href: "/swipe?shortcut=budget", skip: true },
];

const NAV_ITEMS = [
  { href: "/home-screen-v4", label: "Home", icon: "HM" },
  { href: "/explore", label: "Explore", icon: "EX" },
  { href: "/map", label: "Map", icon: "MP" },
  { href: "/appointment-setup", label: "Plan", icon: "PL" },
  { href: "/preferences-setup", label: "Profile", icon: "PF" },
];

export default function HomeScreenV4Page() {
  const pathname = usePathname();
  const [bannerIndex, setBannerIndex] = useState(0);
  const [hasActiveGame] = useState(true);

  useEffect(() => {
    const timer = setInterval(() => {
      setBannerIndex((prev) => (prev + 1) % BANNERS.length);
    }, 4000);
    return () => clearInterval(timer);
  }, []);

  const activeBanner = useMemo(() => BANNERS[bannerIndex], [bannerIndex]);

  return (
    <div className={`${outfit.className} hv4-root`}>
      <div className="hv4-phone">
        <div className="hv4-page">
          <div className="hv4-status">
            <span>9:41</span>
            <span className="hv4-battery" aria-hidden="true">
              <span className="hv4-battery__level" />
              <span className="hv4-battery__cap" />
            </span>
          </div>

          <div className="hv4-content">
            <div className="hv4-shell">
              <header className="space-y-3">
                <div className="flex items-center justify-between">
                  <Link href="/preferences-setup" className="flex items-center gap-2 rounded-2xl border border-zinc-200 bg-zinc-50 px-3 py-2">
                    <Image src="/toast-logo.png" alt="Profile" width={34} height={34} className="rounded-lg border border-zinc-200" />
                    <div>
                      <p className="text-[10px] uppercase tracking-[0.15em] text-zinc-500">Profile</p>
                      <p className="text-sm leading-4 font-bold text-zinc-900">Open Preferences</p>
                    </div>
                  </Link>
                  <span className="rounded-full bg-emerald-100 px-3 py-1 text-xs font-bold text-emerald-700">Live</span>
                </div>

                <div>
                  <p className="text-[11px] uppercase tracking-[0.18em] text-zinc-500">Good evening</p>
                  <h1 className="mt-1 text-[2rem] leading-8 font-extrabold text-zinc-900">Ready to win the food vote?</h1>
                </div>

                <div className="hv4-mode-grid">
                  <div className="hv4-mode-chip">3 MIN TIMER</div>
                  <div className="hv4-mode-chip">ALL MATCH</div>
                  <div className="hv4-mode-chip">INCLUDE PREFS</div>
                  <div className="hv4-mode-chip">GROUP BOOST</div>
                </div>

                <label className="flex items-center gap-2 rounded-2xl border border-zinc-200 bg-zinc-50 px-3 py-2.5">
                  <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" className="h-4 w-4 text-zinc-500">
                    <circle cx="11" cy="11" r="7" strokeWidth="2" />
                    <path d="M20 20l-3.5-3.5" strokeWidth="2" strokeLinecap="round" />
                  </svg>
                  <input type="text" placeholder="Search area or restaurant" className="w-full bg-transparent text-sm text-zinc-800 placeholder:text-zinc-400 focus:outline-none" />
                  <Link href="/explore" className="rounded-lg bg-zinc-900 px-2.5 py-1 text-xs font-bold text-white">
                    Go
                  </Link>
                </label>
              </header>

              <section className="hv4-banner mt-4 p-4">
                <AnimatePresence mode="wait">
                  <motion.div key={activeBanner.id} initial={{ opacity: 0, y: 8 }} animate={{ opacity: 1, y: 0 }} exit={{ opacity: 0, y: -8 }} transition={{ duration: 0.25 }}>
                    <p className="text-xs uppercase tracking-[0.16em] text-emerald-700">Banner</p>
                    <h2 className="mt-1 text-3xl leading-8 font-extrabold text-zinc-900">{activeBanner.title}</h2>
                    <p className="mt-2 text-sm text-zinc-700">{activeBanner.subtitle}</p>
                    <Link href={activeBanner.href} className="mt-3 inline-flex rounded-full bg-zinc-900 px-3 py-1.5 text-xs font-bold text-white">
                      {activeBanner.cta}
                    </Link>
                  </motion.div>
                </AnimatePresence>

                <div className="mt-3 flex gap-1.5">
                  {BANNERS.map((item, index) => (
                    <button
                      key={item.id}
                      aria-label={`banner-${index + 1}`}
                      onClick={() => setBannerIndex(index)}
                      className={`h-2 rounded-full transition-all ${bannerIndex === index ? "w-7 bg-zinc-900" : "w-2 bg-zinc-300"}`}
                    />
                  ))}
                </div>
              </section>

              <section className="mt-4 grid grid-cols-3 gap-2">
                <Link href="/swipe?mode=menu" className="rounded-2xl border border-zinc-200 bg-zinc-50 px-2 py-3 text-center">
                  <p className="text-lg">MN</p>
                  <p className="text-xs font-bold text-zinc-900">Menu</p>
                </Link>
                <Link href="/appointment-setup" className="rounded-2xl border border-zinc-200 bg-zinc-50 px-2 py-3 text-center">
                  <p className="text-lg">AP</p>
                  <p className="text-xs font-bold text-zinc-900">Appointment</p>
                </Link>
                <Link href="/explore" className="rounded-2xl border border-zinc-200 bg-zinc-50 px-2 py-3 text-center">
                  <p className="text-lg">EX</p>
                  <p className="text-xs font-bold text-zinc-900">Explore</p>
                </Link>
              </section>

              {hasActiveGame && (
                <section className="mt-4 rounded-2xl border border-emerald-200 bg-emerald-50 p-3.5">
                  <p className="text-xs uppercase tracking-[0.14em] text-emerald-700">Current game</p>
                  <h3 className="mt-1 text-2xl leading-7 font-extrabold text-zinc-900">Friday Food Squad</h3>
                  <p className="mt-1 text-sm text-zinc-700">4 members - host setting timer - 00:58 left</p>
                  <Link href="/swipe?resume=true" className="mt-3 inline-flex rounded-full bg-emerald-700 px-3 py-1.5 text-xs font-bold text-white">
                    Resume Game
                  </Link>
                </section>
              )}

              <section className="mt-4">
                <div className="mb-2 flex items-center justify-between">
                  <h3 className="text-xs uppercase tracking-[0.18em] text-zinc-700">Trendy picks</h3>
                  <Link href="/explore" className="text-xs font-bold text-emerald-700">View all</Link>
                </div>
                <div className="space-y-2">
                  {TRENDY.map((item) => (
                    <Link key={item.id} href={item.href} className="block rounded-2xl border border-zinc-200 bg-white px-3 py-2.5">
                      <p className="text-lg leading-5 font-bold text-zinc-900">{item.title}</p>
                      <p className="text-sm text-zinc-500">{item.meta}</p>
                    </Link>
                  ))}
                </div>
              </section>

              <section className="mt-4 pb-6">
                <h3 className="mb-2 text-xs uppercase tracking-[0.18em] text-zinc-700">Game shortcuts</h3>
                <div className="space-y-2">
                  {SHORTCUTS.map((item) => (
                    <Link key={item.id} href={item.href} className="flex items-center justify-between rounded-2xl border border-zinc-200 bg-zinc-50 px-3 py-3">
                      <div>
                        <p className="text-lg leading-5 font-bold text-zinc-900">{item.title}</p>
                        <p className="text-sm text-zinc-600">{item.detail}</p>
                      </div>
                      <span className={`rounded-full px-2 py-1 text-[10px] font-bold ${item.skip ? "bg-emerald-100 text-emerald-700" : "bg-zinc-200 text-zinc-700"}`}>
                        {item.skip ? "Skip setup" : "Setup"}
                      </span>
                    </Link>
                  ))}
                </div>
              </section>
            </div>
          </div>

          <Link
            href="/game-setup"
            className="absolute bottom-[86px] right-4 z-20 inline-flex h-14 w-14 items-center justify-center rounded-full bg-emerald-500 text-2xl text-white shadow-[0_16px_30px_rgba(16,185,129,0.35)]"
            aria-label="Create game room"
          >
            +
          </Link>

          <nav className="hv4-nav" aria-label="Bottom navigation">
            {NAV_ITEMS.map((item) => {
              const active = pathname === item.href;
              return (
                <Link key={item.href} href={item.href} className={`hv4-nav__item ${active ? "hv4-nav__item--active" : ""}`}>
                  <span className="hv4-nav__icon" aria-hidden="true">{item.icon}</span>
                  <span className="hv4-nav__label">{item.label}</span>
                </Link>
              );
            })}
          </nav>
        </div>
      </div>
    </div>
  );
}

